<template>
    <div class="container-fluid" >

        <div>
            <transition name="slide-fade">
                <svg id="mexico" viewBox="0 0 800 594" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" v-if="$store.state.mapa" class="mapa">
                    <defs></defs>
                    <g v-if="ban" id="group" stroke="none" stroke-width="1" fill="#6c916f" fill-rule="evenodd" class="map">
                        <g id="map" stroke="#000011" v-for="estado in estados" :key="estado.nombre">
                            <path v-if="estado.activo"  @click="asignarValores(estado)" xmlns="http://www.w3.org/2000/svg" :title="estado.nombre" :id="estado.identificador" class="land presencia" :d="estado.coordenadas"/>
                            <path v-if="!estado.activo" @click="asignarValores(estado)" xmlns="http://www.w3.org/2000/svg" :title="estado.nombre" :id="estado.identificador" class="land" :d="estado.coordenadas"/>
                        </g>
                    </g>
                </svg>
            </transition>
        </div>
        

        <div class="textoPrincipal">
            Nos encontramos en los siguientes estados:<br>
            <ul>
                <li v-for="estado in estadosActivos" :key="estado.nombre">
                    {{ estado.nombre }}    
                </li>
            </ul>
        </div>

        <div v-if="$store.state.presencia">
            <div class="ciudades" v-if="banClick" >
                <p> {{ $store.state.province }} </p>
                <p> {{ $store.state.presencia }} </p>
                <div v-if="banCiudades">
                    <li v-for="ciudad in $store.state.ciudadesConPresencia" :key="ciudad"> {{ciudad}} </li>
                </div>         
            </div>
        </div>
        
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        data() {
            return{
                province: '',
                presencia: '',
                estados:null,
                ban:0,
                banClick:0,
                banCiudades:0,
                //Array con el nombre de las ciudades de los estados con precensia (Dinámico)
                ciudadesConPresencia:null,
                //Array con el nombre de los estados donde se tiene precencia
                estadosConPrecencia:[]

            }
        },

        created(){
            
              try{
                axios.get('https://app-merc.herokuapp.com/api/mapa/listar').
                then(response=>
                    {
                        if(response.status===200){
                            this.estados = response.data;
                            
                            console.log(this.estados)
                            for (var i = 0 ; i < this.estados.length; i++){
                                if(this.estados[i].activo){
                                    this.estadosConPrecencia[i] = this.estados[i].nombre;
                                }
                                
                            }
                            console.log(this.estadosConPrecencia)
                            this.ban = 1;
                        }
                    })
            }catch(e){
                console.log("Error al extraer los estados");
                console.log(e)
            }
        },
    

        mounted () {
            let customScript = document.createElement('script')
            customScript.setAttribute('src', 'https://cdnjs.cloudflare.com/ajax/libs/lodash.js/4.14.1/lodash.min.js') 
            document.head.appendChild(customScript)
        },

        methods: {
            asignarValores(estadoClick){
                
                this.$store.state.province = estadoClick.nombre;
                if (estadoClick.activo){
                    
                    this.$store.state.presencia  = 'Tenemos presencia en las siguientes ciudades:'
                    //El [0] es porque cargué mal las ciudades en la base de datos jiji
                    this.$store.state.ciudadesConPresencia= estadoClick.ciudades[0].split(',');
                    this.banCiudades = 1;

                }else{

                    this.$store.state.presencia = 'Pronto estaremos en tu ciudad!'
                    this.banCiudades = 0;
                }
                this.banClick = 1;
            }
        },
        computed:{
            estadosActivos () {
                if(this.ban){
                    return this.estados.filter(i => i.activo === true)
                }               
            }
        }
    }

</script>

