<template>
      
  <div class="container">
    
    <carousel-3d class="carousel-3d-container imagen"  :width="360" :height="240"  :controls-visible="flecha" :autoplay-timeout="1000" :controls-prev-html="'&#10092; '" :controls-next-html="'&#10093;'" :controls-width="30" :controls-height="60" >
      
      <slide v-for="(slide, i) in slides" :index="i" :key="i">
        <figure>
          <img src="https://images.pexels.com/photos/3182759/pexels-photo-3182759.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260" class="crop-image" alt="zero">
        </figure>
      </slide>
     
    </carousel-3d>
   
  </div>

    
          
</template>

<script>
import { Carousel3d, Slide } from 'vue-carousel-3d';
  export default {
    components: {
        Carousel3d,
        Slide 
    },
    data(){
      return{
            slides: [ 1, 2, 3, 4, 5],
            window:{
                alto:0,
                ancho:0
            },
            flecha:false           
      }
    },
    created() {
        window.addEventListener('resize', this.handleResize);
        this.handleResize();
        
    },
    destroyed() {
        window.removeEventListener('resize', this.handleResize);
    },
    methods: {
        handleResize() {
            this.window.ancho = window.innerWidth;
            this.window.alto = window.innerHeight;
            if(this.window.ancho < 576){
              this.flecha = true
            }else{
              this.flecha = false
            }
        }
    }
  };
</script>

<style scoped>

  .carousel-3d-container figure {
    margin: 0;
  }

  @media only screen and (max-width: 767px) {
    .imagen{
      transform: none;
    }
  }

</style>